@font-face {
    font-family: Font1;
    src: url(/fonts/2204.ttf);
}

@font-face {
    font-family: Font2;
    src: url(/fonts/3955.ttf);
}

@font-face {
    font-family: Font3;
    src: url(/fonts/2211.ttf);
}

* {
    box-sizing: border-box;
    outline: 1px solid black;
    margin: 0;
    padding: 0;
}

.auto {
    margin: 0 auto;
}

.header, .slider, .portfolio, .service, .comments, .contacts, .footer {
    max-width: 1920px;
}

.nav, .links, .box {
    max-width: 1170px;
}

.header {
    background: rgb(19, 18, 18);
}

.nav {
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav a {
    text-decoration: none;
    color: white;
}

.nav a:hover {
    border-bottom: 6px solid red;
}

.item {
    margin-right: 55px;
    font-size: 17px;
    font-family: Font1;  
}

.item:last-child {
    margin-right: 0;
}
// end header

.slider {
    position: relative;
    height: 622px;
    padding: 100px 0;
    box-sizing: border-box;
    background: url(/img/slider.jpg);   
}

.colored_squares {
    position: absolute;
    top: 25px;
    left: 66px;
    display: flex;
    flex-wrap: wrap;
    width: 242px;
}

.color {
    width: 100px;
    height: 100px;
    margin: 8px;
}

.black {
    background-color: black;
}

.gray {
    background-color: gray;
}

.blue {
    background-color: rgb(29, 117, 158);
}

.light_blue {
    background-color: rgb(91, 169, 233);
}

.red {
    background-color: rgb(199, 1, 1);
}

.light_red {
    background-color: rgb(247, 35, 28);
}

.links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

