.style_links {
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.style__links {
    transform: rotate(45deg);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 190px;
    height: 190px;
    background: rgba(309, 237, 308, 0.8);
}

.style__links img {
    transform: rotate(-45deg);
}

.style__links img:hover {
    transform: scale(1.2) rotate(-45deg);
}

.opasity {
    background: rgba(201, 215, 221, 0.6);
}
// end slider

.box {
    padding: 62px 0px 74px 0px;
}

.h2 {
    font-size: 33px;
    font-family: Font2;
}

.text {
    font-family: Font3;
    font-size: 15px;
    line-height: 23px;
    padding-top: 23px;
}

.submenu {
    display: flex;
    font-family: Font2;
    font-size: 17px; 
    padding: 28px 0px;
}

.subitem {
    padding-bottom: 5px;
    margin-right: 46px;
}

.submenu a {
    text-decoration: none;
    color: black;
}

.submenu a:hover {
    border-bottom: 6px solid red; 
}

.img {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.img_portfolio {
    height: 200px;
    width: 200px;
    border: 1px solid gray;
}

.siding1 {
    background: url(/img/siding1.jpg) -29px 0px;
}

.siding2 {
    background: url(/img/siding2.jpg);
}

.siding3 {
    background: url(/img/siding3.jpg) -10px 0px;
}

.siding4 {
    background: url(/img/siding4.jpg) -10px 0px;
}

.siding5 {
    background: url(/img/siding5.jpg) -4px 0px;
}

.open {
    display: inline-block;
    font-size: 15px;
    padding: 10px 15px;
    margin-top: 39px;
    border: 3px solid #d83030;
    font-family: Font2;
}

.open:hover {
    background: #d83030;
    color: white;
}

// end portfolio

.service {    
    background-color: #e8e7e7;
}

.img_service {
    height: 270px;
    width: 270px;
    border: 1px solid gray;
}

.gr_design1 {
    background: url(/img/gr_design1.jpg) -149px -77px;
}

.gr_design2 {
    background: url(/img/gr_design2.jpg) -72px -22px;
}

.gr_design3 {
    background: url(/img/gr_design3.jpg) -161px -10px;
}

.gr_design4 {
    background: url(/img/gr_design4.jpg) -185px -26px;
}

// end service

.comments {
    background: rgb(56, 55, 55);
    color: white;
}

.foto {
    margin: 32px 30px 0px 0px;
    width: 170px;
    height: 140px;
    float: left;
    background: url(/img/foto.jpg) -48px -14px;
}

h4 {
    font-size: 20px;
    font-family: Font2;
    padding-top: 26px;
}

h6 {
    font-family: Font2;
    font-style: italic;
    font-size: 12px;
    padding-top: 3px;
}

.text_comments {
    padding-right: 400px;
    font-family: Font3;
    font-size: 15px;
    line-height: 23px;
    padding-top: 17px;
}

.next {
    display: inline-block;
    position: relative;
    top: 35px;
    left: 202px;
    margin-bottom: 35px;
}

.next a {
    display: inline-block;
    height: 23px;
    width: 23px;
    background: gray;
    margin-right: 6px;
    color: white;
    text-decoration: none;
    text-align: center;
    line-height: 11px;
    font-size: 48px;
}

// end comments

.contacts {
    background: black;
}

.box_contacts {
    padding-top: 31px;
    color: white;
}

.address {
    display: inline-block;
    width: 405px;
    padding: 30px 30px 20px 40px;
    font-family: Font2;
    background: #b51717;
}

.contacts h2 {
    color: white;
}

h3 {
    font-size: 26px;
}

.text_contacts {
    white-space: pre;
    font-size: 19px;
    padding-top: 13px;
}